<template>
    <div class="goods">
        <div class="menu-wrapper" ref="menuWrapper">
            <ul>
                <li v-for="item in goods" class="menu-item">
                    <span class="text">
                        <span v-show="item.type>0" class="icon" :class="classMap[item.type]"></span>{{item.name}}
                    </span>
                </li>
            </ul>
        </div>
        <div class="foods-wrapper" ref="foodsWrapper">
            <ul>
                <li v-for="item in goods" class="food-list">
                    <h1 class="title">{{item.name}}</h1>
                    <ul>
                        <li v-for="food in item.foods" class="food-item">
                            <div class="icon">
                                <img width="57" height="57" :src="food.icon">
                            </div>
                            <div class="content">
                                <h2 class="name">{{food.name}}</h2>
                                <p class="desc">{{food.description}}</p>
                                <div class="extra">
                                    <span class="count">月售{{food.sellCount}}份</span>
                                    <span>好评率{{food.rating}}%</span>
                                </div>
                                <div class="price">
                                    <span class="now">¥{{food.price}}</span>
                                    <span  class="old" v-show="food.oldPrice">¥{{food.oldPrice}}</span>
                                </div>
                            </div>
                        </li>
                    </ul>
                </li>
            </ul>
        </div>
    </div>
</template>

<script>
    import BScroll from 'better-scroll';

    export default {
       data () {
           return {
               goods:[],

           };
        },
       created() {
           this.axios({
               url:'/testfunction.php',
               method:'post',
               headers:{
                   "Content-type":"application/json"
               }
           }).then((res)=>{
               this.goods = res.data.goods;
               this.$nextTick(()=>{
                   this._initScroll();
               });

           });
           this.classMap = ['decrease','discount','special','invoice','guarantee'];

       },
       methods: {
           _initScroll(){
//               先拿左侧栏,第一个参数是DOM，第二个是json
               this.menuScroll = new BScroll(this.$refs.menuWrapper,{});
               this.foodsScroll = new BScroll(this.$refs.foodsWrapper,{});
           }
       }
    }
</script>

<style>
    .goods {
        display: flex;
        position: absolute;
        width: 100%;
        top:174px;
        bottom:46px;
        overflow: hidden;

    }
    .goods .menu-wrapper {
        flex:0 0 80px;
        width: 80px;
        background: #f3f5f7;
    }
    .goods .menu-wrapper .menu-item{
        display: table;
        width: 56px;
        height:54px;
        line-height: 14px;
        padding:12px;
    }
    .goods .menu-wrapper .menu-item .text{
        display: table-cell;
        width:56px;
        vertical-align: middle;
        font-size:12px;


    }
    .goods .menu-wrapper .menu-item .icon{
        display: inline-block;
        vertical-align: top;
        width: 12px;
        height:12px;
        margin-right: 4px;
        background-size:12px 12px;
        background-repeat: no-repeat;
    }
    .goods .menu-wrapper .menu-item .text .decrease {
        background-image: url(./decrease_3@2x.png);
    }
    .goods .menu-wrapper .menu-item .text .discount {
        background-image: url(./discount_3@2x.png);
    }
    .goods .menu-wrapper .menu-item .text .special {
        background-image: url(./special_3@2x.png);
    }
    .goods .menu-wrapper .menu-item .text .invoice {
        background-image: url(./invoice_3@2x.png);
    }
    .goods .menu-wrapper .menu-item .text .guarantee {
        background-image: url(./guarantee_3@2x.png);
    }
    .goods .foods-wrapper {
        flex:1;
    }
    .goods .foods-wrapper .title{
        padding-left:14px;
        height:26px;
        line-height: 26px;
        border-left:2px solid #d9dde1;
        font-size:12px;
        color:rgb(147,153,159);
        background: #f3f5f7;
    }
    .goods .foods-wrapper .food-item {
        display: flex;
        margin: 18px;
        border-bottom: 1px solid rgba(7,17,27,0.1);
        padding-bottom:18px;
    }
    .goods .foods-wrapper .food-item:last-child {
        border-bottom:0;

    }
    .goods .foods-wrapper .food-item .icon {
        flex:0 0 57px;
        margin-right: 10px;
    }
    .goods .foods-wrapper .food-item .content {
        flex: 1;
    }
    .goods .foods-wrapper .food-item .content .name {
        margin: 2px 0 8px 0;
        height:14px;
        line-height: 14px;
        font-size: 14px;
        color: rgb(7,17,27);
    }
    .goods .foods-wrapper .food-item .content .desc, .extra {
        line-height: 10px;
        font-size: 10px;
        color: rgb(147,153,159);
    }
    .goods .foods-wrapper .food-item .content .desc {
        margin-bottom: 8px;
        line-height: 12px;

    }
    .goods .foods-wrapper .food-item .content .extra {
        line-height: 10px;
    }
    .goods .foods-wrapper .food-item .content .extra .count {
        margin-right: 12px;
    }
    .goods .foods-wrapper .food-item .content .price {
        font-weight: 700;
        line-height: 24px;

    }
    .goods .foods-wrapper .food-item .content .price .now {
        margin-right: 8px;
        font-size: 14px;
        color: rgb(240,20,20);

    }
    .goods .foods-wrapper .food-item .content .price .old {
        text-decoration: line-through;
        font-size:10px;
        color: rgb(147,153,159);
    }
</style>







